.loading {
    background: #fffefe;
    width: 100%;
    height: 100%;
    min-height: 40vh;
}

.loading .img {
    max-width: 180px;
    margin: 0 auto;
    margin-top: 10%;
}

.loading .img img {
    animation-name: spin;
    animation-duration: 1000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear; 
}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}