@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Infant:ital,wght@0,300;0,400;0,500;1,300&display=swap);
/* Fonts */
/* font-family: 'Cinzel', serif; */
/* font-family: 'Cormorant Infant', serif; */

/* Global Rulesets, Resets */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::selection {
    background: rgba(0, 0, 0, 0.3);
}

*:before,
*:after {
    box-sizing: border-box;
}

html {
    height: 100%;
}
.app {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}
/* This stylesheet contains the color palette for the site. Each color can be applied to various containers */
/* font colors are preceded by font- in their naming */

/* bg colors */
.white {
    background: #fdfdfd;
}

.pure-white {
    background: white;
}

.light-gray {
    background: #f0f0f0;
}

.gray {
    background: #adabab;
}

.black {
    background: #000000;
}

.burgundy {
    background: #7a5963;
}

/* font colors */
.font-white {
    color: #fdfafa;
}

.font-dark {
    color: #0e0d0d;
}

.font-gray {
    color: #a7a4a4;
}

.font-dark-gray {
    color: #686666;
}
/* nav header */
.navbar {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    height: auto;
    z-index: 2;
}
.navbar .container {
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
    border-bottom: 0.2px solid #e0dfdf;
}

/* Promo Banner */
.nav-promo-banner {
    font-family: "Century Gothic Std", Arial, sans-serif;
    font-size: 0.72em;
    text-align: center;
    padding: 8px 0;
    width: 100%;
    margin: 0 auto;
    text-transform: uppercase;
}
.nav-promo-banner span {
    display: inline-block;
    max-width: 90%;
    margin: 0 auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* Large Navbar */
.nav-lg .top {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    box-sizing: border-box;
    margin-bottom: 20px;
    padding-top: 15px;
}

.nav-lg .top-left {
    flex-basis: 10%;
}

.nav-lg .logo-center {
    display: flex;
    flex:1 1;
    flex-basis: 80%;
    justify-content: center;
}
.nav-lg .logo-center a {
    margin: 0;
    padding: 0;
}
.nav-lg .logo-center img {
    max-height: 2.25em;
}

.nav-lg .top-right {
    font-family: "Century Gothic Std", Arial, sans-serif;
    display: flex;
    justify-content: flex-end;
    z-index: 1;
    flex-basis: 10%;
}
.nav-lg .top-right .nav-profile,
.nav-lg .top-right .nav-bag {
    text-align: center;
    display: inline-block;
    margin: 0 12px;
}
.nav-lg .top-right .nav-profile img,
.nav-lg .top-right .nav-bag img {
    display: block;
    max-height: 1.8em;
    margin: 0 auto;
}
.nav-lg .top-right .nav-bag .nav-shopping-qty {
    display: inline-block;
    text-align: center;
    font-size: 0.8em;
    color: white;
    position: fixed;
    top: 58px;
    z-index: 3;
    background: rgb(0, 0, 0);
    padding: 0 4px;
    border-radius: 50%;
}

.nav-lg .nav-bottom {
    position: relative;
}
.nav-lg .bottom .nav-btm-tabs {
    list-style-type: none;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    padding: 0;
    font-family: "Century Gothic Std", Arial, sans-serif;
}
.nav-lg .bottom .nav-btm-tabs .nav-btm-tab {
    display: inline-block;
    margin: 0 auto;
    padding: 0;
    margin-right: 60px;
}
.nav-lg .bottom .nav-btm-tabs .nav-btm-tab:last-child {
    margin-right: 0;
}
.nav-lg .bottom .nav-tab-link {
    display: inline-block;
    font-size: 0.9em;
    text-decoration: none;
    text-transform: uppercase;
    color: inherit;
    border-bottom: 1.5px solid #ffffff00;
    margin: 5px 0;
}
.nav-lg .bottom .nav-tab-link:hover, .nav-tab-hovered {
    font-weight: bold;
    border-bottom: 1.5px solid black !important;
}

/* Large Navbar - Product Menu */
.prod-menu-lg {
    font-family: "Century Gothic Std", Arial, sans-serif;
    position: absolute;
    left: 0;
    top: 130px;
    background: #fdfdfd;
    width: 100%;
    margin: 0 auto;
    padding: 25px 0;
}
.prod-menu-lg .pml-container {
    max-width: 90%;
    margin: 0 auto;
}
.prod-menu-lg .nav-prod-list {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.prod-menu-lg .product-category {
    display: inline-block;
    vertical-align: top;
    margin-right: 40px;
}
.prod-menu-lg .product-category:first-child {
    margin-right: 70px;
}
.prod-menu-lg .product-category:last-child {
    margin-right: 0;
}
.prod-menu-lg .product-category-title {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0;
    padding: 0;
}
.prod-menu-lg .product-category-title a {
    color: black;
    text-decoration: none;
    border-bottom: 1.3px solid rgba(0, 0, 0, 0);
}
.prod-menu-lg .product-category-title a:hover {
    border-bottom: 1.3px solid rgb(0, 0, 0);
}
.prod-menu-lg .product-cat-img {
    display: block;
    margin-top: 20px;
}
.prod-menu-lg .product-cat-img img {
    max-height: 200px;
}

.prod-menu-lg .product-subcategories {
    list-style-type: none;
    margin-top: 20px;
}
.prod-menu-lg .product-subcat-link {
    padding-bottom: 4px;
    width: 120px;
}
.prod-menu-lg .product-subcat-link a {
    color: #4e4e4e;
    text-decoration: none;
    font-size: 0.8em;
    letter-spacing: 0.4px;
}
.prod-menu-lg .product-subcat-link a:hover {
    color: #000000;
    text-shadow: -0.01px -0.01px rgb(56, 56, 56);
}




/* Small Navbar */
.nav-sm .top {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    box-sizing: border-box;
    margin-bottom: 20px;
    padding-top: 15px;
}

.nav-sm .top-left {
    flex-basis: 10%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}
.nav-sm .hamburger {
    cursor: pointer;
    align-items: center;
    vertical-align: middle;
}
.nav-sm .hamburger img {
    max-height: 2.3em;
}

.nav-sm .logo-center {
    display: flex;
    flex:1 1;
    flex-basis: 80%;
    justify-content: center;
}
.nav-sm .logo-center a {
    margin: 0;
    padding: 0;
}
.nav-sm .logo-center img {
    max-height: 2.25em;
}

.nav-sm .top-right {
    font-family: "Century Gothic Std", Arial, sans-serif;
    display: flex;
    justify-content: flex-end;
    z-index: 1;
    flex-basis: 10%;
}
.nav-sm .top-right .nav-profile,
.nav-sm .top-right .nav-bag {
    text-align: center;
    display: inline-block;
    margin: 0 12px;
}
.nav-sm .top-right .nav-profile img,
.nav-sm .top-right .nav-bag img {
    display: block;
    max-height: 1.8em;
    margin: 0 auto;
}
.nav-sm .top-right .nav-bag .nav-shopping-qty {
    display: inline-block;
    text-align: center;
    font-size: 0.8em;
    color: white;
    position: fixed;
    top: 58px;
    z-index: 3;
    background: rgb(0, 0, 0);
    padding: 0 4px;
    border-radius: 50%;
}

/* Small Navbar - Product Menu */
.prod-menu-sm {
    position: fixed;
    height: 100%;
    overflow-y: scroll;
    top: 0;
    left: 0;
    z-index: 5;
}
.prod-menu-sm .pms-container {
    width: 600px;
}
.prod-menu-sm .navigation-panel {
    overflow: hidden;
    box-sizing: border-box;
    padding: 10px 20px;
    font-family: "Century Gothic Std", Arial, sans-serif;
}

.prod-menu-sm .menu-header {
    margin: 10px 0;
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
    border-bottom: 0.2px solid #e0dfdf;
    margin-bottom: 25px;
}
.prod-menu-sm .menu-header span,
.prod-menu-sm .menu-header button {
    display: inline-block;
    vertical-align: middle;
}
.prod-menu-sm .menu-header-title {
    display: flex;
    flex-basis: 90%;
    justify-content: center;
    align-items: center;
}
.prod-menu-sm .menu-header-close {
    display: flex;
    flex-basis: 10%;
    justify-content: flex-end;
    align-items: center;
}
.prod-menu-sm .menu-header span {
    text-transform: uppercase;
    font-size: 0.95em;
    text-align: center;
}
.prod-menu-sm .menu-header button {
    border: 0;
    padding: 0;
    background: none;
    cursor: pointer;
    outline: none;
}
.prod-menu-sm .menu-header button img {
    max-height: 1.5em;
}

.prod-menu-sm .gender-options,  
.prod-menu-sm .categories,
.prod-menu-sm .subcategories {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.prod-menu-sm .categories {
    margin-top: 15px;
}

.prod-menu-sm .expand-list {
    border: 0;
    padding: 0;
    background: none;
    cursor: pointer;
    outline: none;
    display: block;
    margin: 5px 0;
    padding-top: 20px;
    padding-bottom: 5px;
    width: 100%;
    border-bottom: 0.2px solid #e0dfdf;
    display: flex;
}
.prod-menu-sm .expand-list .list-label,
.prod-menu-sm .expand-list .expand-icon {
    display: inline-block;
}
.prod-menu-sm .expand-list .list-label {
    text-transform: uppercase;
    text-align: left;
    letter-spacing: 0.6px;
    font-size: 1.3em;
    font-weight: 100;
    flex-basis: auto;
    width: 100%;
}
.prod-menu-sm .expand-list .expand-icon {
    flex-basis: auto;
    max-width: 20px;
    margin-right: 5px;
}
.prod-menu-sm .expand-list.collapsed .expand-icon {
    transform: rotate(180deg);
}
.prod-menu-sm .categories.collapsed {
    display: none;
}
.prod-menu-sm .categories.expanded {
    display: block;
}

.prod-menu-sm .category .category-title a {
    text-decoration: none;
    text-transform: uppercase;
    color: black;
    font-size: 1.3em;
    font-weight: 200;
    display: inline-block;
    width: 100%;
}
.prod-menu-sm .category .category-title a:hover {
    text-shadow: -0.01px -0.01px rgb(0, 0, 0);
}

.prod-menu-sm .img-item {
    margin-top: 30px;
    padding-bottom: 20px;
    border-top: 0.2px solid #e0dfdf;
}
.prod-menu-sm .img-item .img-item-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.prod-menu-sm .img-item .category-title {
    padding-top: 10px;
    padding-bottom: 20px;
    font-weight: lighter;
    font-size: 11px;
}
.prod-menu-sm .img-item img {
    max-height: 200px;
}

.prod-menu-sm .subcategories {
    margin-top: 2px;
    margin-bottom: 25px;
}
.prod-menu-sm .subcategory a {
    color: rgb(88, 88, 88);
    text-transform: uppercase;
    text-decoration: none;
    font-size: 0.9em;
    line-height: 2em;
    margin: 4px 0;
    display: inline-block;
    width: 100%;
}
.prod-menu-sm .subcategory a:hover {
    color: #000000;
    text-shadow: -0.01px -0.01px rgb(56, 56, 56);
}




/* media queries */
@media (max-width: 1000px) {
    .navbar .container {
        width: 90%
    }
    .nav-sm .top-left {
        margin-right: 10px;
    }
    .nav-sm .hamburger img {
        max-height: 1.9em;
    }
    .nav-sm .logo-center {
        justify-content: flex-start;
    }
    .nav-sm .logo-center img {
        max-height: 1.65em;
    }
    .nav-sm .top-right .nav-bag .nav-shopping-qty {
        font-size: 0.7em;
        top: 53px;
    }
}

@media (max-width: 768px) {
    .navbar .container {
        width: 95%
    }
    .prod-menu-sm {
        width: 100%;
    }
    .prod-menu-sm .pms-container {
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        min-width: 100%;
    }
}
@media (max-width: 485px) {
    .nav-promo-banner {
        font-size: 0.55em;
    }
    .nav-sm .top-left {
        margin-right: 5px;
    }
    .nav-sm .hamburger img {
        max-height: 1.5em;
    }
    .nav-sm .logo-center {
        flex-basis: auto;
    }
    .nav-sm .logo-center img {
        max-height: 1.25em;
    }
    .nav-sm .top-right .nav-profile img, .nav-sm .top-right .nav-bag img {
        max-height: 1.4em;
    }
    .nav-sm .top-right .nav-bag .nav-shopping-qty {
        font-size: 0.7em;
        top: 50px;
    }
    .nav-sm .top-right .nav-profile, .nav-sm .top-right .nav-bag {
        margin: 0;
    }
    .nav-sm .top-right .nav-profile:first-child {
        margin-right: 6px;
    }
}
.footer {
    border-top: 0.2px solid #e0dfdf;
    overflow: hidden;
    width: 100%;
    padding: 30px 0;
    font-family: "Century Gothic Std", Arial, sans-serif;
    margin-top: auto;
}

.footer .container {
    max-width: 75%;
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
}

.footer .footer-links {
    display: flex;
    flex-direction: row;
    vertical-align: top;
    align-content: top;
}
.footer .logo {
    margin-right: 60px;
}
.footer .logo a {
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.footer .logo img {
    max-height: 2em;
}
.footer .links ul {
    list-style-type: none;
    border-left: 0.2px solid #e0dfdf;
    padding-left: 40px;
}
.footer .links li {
    margin-bottom: 1.3em;
}
.footer .links a {
    text-decoration: none;
    color: #333333;
}

.footer .project-stack {
    margin-bottom: 12px;
    font-size: 0.85em;
}
.footer .disclaimer {
    font-size: 0.7em;
}


/* Media Queries */
@media (max-width: 768px) {
    .footer .footer-links {
        flex-direction: column;
    }
    .footer .links ul {
        margin-top: 20px;
    }
}
@media (max-width: 450px) {
    .footer .logo img {
        max-height: 1.3em;
    }
    .footer .links ul {
        font-size: 0.8em;
    }
}
.homepage {
    overflow: hidden;
    box-sizing: border-box;
    padding: 25px 0;
    font-family: "Century Gothic Std", Arial, sans-serif;
}

.homepage .container {
    margin: 0 auto;
    overflow: hidden;
    max-width: 75%;
}

/* Jumbotron */
.homepage .jumbotron {
    overflow: hidden;
    width: 100%;
    margin-bottom: 50px;
}
.homepage .jumbotron .jumbo-img {
    min-height: 500px;
    max-height: 500px;
    content: "";
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-image: url("/images/promo/general/homepage-main.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top right;
}
.homepage .jumbotron .jumbo-btm {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 15px;
    vertical-align: center;
    align-items: center;
    background: #8080801f;
}
.homepage .jumbotron .jumbo-title {
    font-size: 1.7em;
    font-weight: 100;
    text-transform: uppercase;
    font-family: 'Cormorant Infant', serif;
}
.homepage .jumbotron .jumbo-btns {
    display: inline-block;
}
.homepage .jumbotron .jumbo-btns button {
    padding: 18px 30px;
    border: 1px solid #e2e2e2;
    border-radius: 10px;
    background: #000000;
    cursor: pointer;
    outline: none;
    color: white;
    text-transform: uppercase;
}
.homepage .jumbotron .jumbo-btns button:hover {
    box-shadow: 0.1px 0.1px #303030;
    border: 1px solid #949494;
    background: #f7f7f7;
    color: black;
}
.homepage .jumbotron .jumbo-btns button:first-child {
    margin-right: 10px;
}
.homepage .jumbotron .jumbo-btns button a {
    text-decoration: none;
    text-transform: uppercase;
    color: inherit;
    font-size: 1.1em;
}

/* Men and Women's Fashion Categories */
.homepage .fashion-categories {
    margin-bottom: 50px;
    border-top: 0.2px solid #d3d3d3;
}
.homepage .gender-title {
    text-align: center;
    padding: 30px 0;
}
.homepage .gender-title h1 {
    font-weight: 100;
    font-size: 2em;
    text-transform: uppercase;
    font-family: 'Cormorant Infant', serif;
}
.homepage .categories-gallery {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    box-sizing: border-box;
}

.homepage .category-item {
    margin: 10px;
    opacity: 1.0;
    flex-basis: 25%;
}
.homepage .category-item:hover {
    opacity: 0.8;
}
.homepage .category-item .img-wrapper {
    width: 100%;
    height: 17vw;
    overflow: hidden;
}
.homepage .category-item img {
    width: 105%;
    height: 17vw;
    object-fit: fill;
    object-position: bottom left;
}
.homepage .category-item a {
    text-transform: uppercase;
    text-decoration: none;
    color: black;
}
.homepage .category-item h3 {
    font-weight: 100;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 5px;
}


/* Media Queries */
@media (max-width: 1000px) {

    .homepage .container {
        max-width: 90%;
    }    

    .homepage .jumbotron .jumbo-btm {
        flex-direction: column;
    }
    .homepage .jumbotron .jumbo-title {
        margin-bottom: 10px;
    }
}
@media (max-width: 768px) {

    .homepage .container {
        max-width: 95%;
    }
    
    .homepage .categories-gallery {
        flex-direction: column;
    }
    .homepage .category-item .img-wrapper {
        height: auto;
    }
    .homepage .category-item img {
        width: 100%;
        height: auto;
        object-fit: fill;
        object-position: bottom left;
    }
}
@media (max-width: 500px) {

    .homepage .jumbotron .jumbo-img {
        min-height: 300px;
        max-height: 300px;
    }
    
    .homepage .jumbotron .jumbo-title {
        font-size: 1.2em;
    }
    .homepage .jumbotron .jumbo-btns button {
        padding: 12px 15px;
    }
}
@media (max-width: 400px) {
    

    .homepage .jumbotron .jumbo-btns button {
        font-size: 0.8em;
    }
}
.gender-page {
    overflow: hidden;
    box-sizing: border-box;
    padding: 25px 0;
}

.gender-page .container {
    width: 75%;
    max-width: 75%;
    margin: 0 auto;
    overflow: hidden;
}

.gender-page .banner {
    width: 100%;
    position: relative;
    margin-bottom: 25px;
}
.gender-page .banner-img {
    width: 100%;
    height: 12vw;
    background: black;
    z-index: 0;
}
.gender-page .banner-img img {
    width: 100%;
    height: 12vw;
    opacity: 0.7;
    object-fit: cover;
    object-position: center;
}
.gender-page .banner h1 {
    font-family: 'Cormorant Infant', serif;
    text-align: center;
    color: white;
    font-weight: 200;
    font-size: 5.2vw;
    padding: 0;
    text-shadow: 2px 1px 2px black;
    position: absolute;
    z-index: 1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.gender-page .featured {
    width: 100%;
    position: relative;
    margin-bottom: 25px;
    background: black;
}
.gender-page .featured .featured-img {
    width: 100%;
    max-height: 35vw;
}
.gender-page .featured .featured-img img {
    width: 100%;
    height: 35vw;
    object-fit: cover;
    object-position: top center;
}
.gender-page .featured:hover .featured-img img {
    opacity: 0.6;
}
.gender-page .featured h1 {
    font-family: 'Cormorant Infant', serif;
    text-align: center;
    color: white;
    font-weight: 200;
    font-size: 3.2vw;
    padding: 0;
    text-shadow: 2px 1px 2px black;
    position: absolute;
    z-index: 1;
    left: 10%;
    top: 90%;
    transform: translate(-10%, -90%);
}

.gender-page .popular-items {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
.gender-page .popular-items .item {
    max-width: 50%;
    overflow: hidden;
    position: relative;
    background: black;
    height: 62vw;
    margin: 20px 0;
}
.gender-page .popular-items .item:first-child {
    margin-right: 12.5px;
}
.gender-page .popular-items .item:last-child {
    margin-left: 12.5px;
}
.gender-page .popular-items .item img {
    width: 100%;
